<template>
  <transition>
    <div class="navbar" :class="{ collapse: Collapse }">
      <div class="lump" @click="DoExpand">
        <div class="inner">
          <div class="bg" />
          <i class="el-icon-arrow-left" />
        </div>
      </div>
      <div class="scene" v-if="!Collapse">
        <div class="title">{{ Title }}</div>
        <el-scrollbar>
          <el-menu
            ref="NavMenu"
            menu-trigger="hover"
            :router="true"
            :default-active="this.$route.meta.path || this.$route.path"
            @open="MenuOpen"
            @close="MenuClose"
          >
            <template v-for="menu in Menu">
              <el-submenu
                :index="menu.ModuleWebURL"
                :popper-append-to-body="false"
                :key="menu.ModuleCode"
                v-if="menu.SubModule && menu.SubModule.length > 0"
              >
                <template slot="title">
                  <i class="iconfont" :class="menu.ModuleParameter1"></i>
                  <span slot="title">{{ menu.ModuleName }}</span>
                </template>
                <el-menu-item
                  v-for="subMenu in menu.SubModule"
                  :index="subMenu.ModuleWebURL"
                  :key="subMenu.ModuleCode"
                >
                  <i class="iconfont" :class="subMenu.ModuleParameter1"></i>
                  {{ subMenu.ModuleName }}
                </el-menu-item>
              </el-submenu>
              <el-menu-item
                :index="menu.ModuleWebURL"
                v-else
                :key="menu.ModuleCode"
              >
                <i class="iconfont" :class="menu.ModuleParameter1"></i>
                {{ menu.ModuleName }}
              </el-menu-item>
            </template>
          </el-menu>
        </el-scrollbar>
      </div>
    </div>
  </transition>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";

export default {
  props: {
    Collapse: {
      type: Boolean,
      default: false
    },
    Title: {
      type: String,
      default: ""
    },
    Code: {
      type: String,
      default: ""
    }
  },
  watch: {
    Code(newV, oldV) {
      this.InitMenu();
    }
  },
  data() {
    return {
      Menu: [],
      IsCollapse: this.Collapse
    };
  },
  mounted() {
    this.InitMenu();
  },
  methods: {
    DoExpand() {
      this.IsCollapse = !this.IsCollapse;
      this.$emit("update:collapse", this.IsCollapse);
    },
    async InitMenu() {
      this.Menu = [];
      const mp = store.GetMP();
      if (mp) {
        const res = await api.Module.GetModuleTree();

        const isAdmin = store.GetUserInfo().IsSupperAdmin;

        this.Menu = [];

        if (res) {
          if (res.Datas) {
            const FindModule = res.Datas.find(
              item => item.ModuleCode === this.Code
            );
            if (FindModule) {
              const SubModule = JSON.parse(FindModule.SubModule);
              if (SubModule && SubModule.length > 0) {
                for (let j = SubModule.length - 1; j >= 0; j--) {
                  const sm = SubModule[j];
                  if (isAdmin || (mp[sm.ModuleCode] & 1) === 1) {
                    if (sm.SubModule && sm.SubModule.length > 0) {
                      for (let k = sm.SubModule.length - 1; k >= 0; k--) {
                        const ssm = sm.SubModule[k];
                        if (isAdmin || (mp[ssm.ModuleCode] & 1) === 1) {
                        } else {
                          sm.SubModule.splice(k, 1);
                        }
                      }
                    }
                    if (sm.ModuleCode === "PEC") {
                      const res = await api.Metadata.GetTrees("JPKCLM");
                      if (res.Result) {
                        if (!sm.SubModule) sm.SubModule = [];
                        res.Datas.forEach(element => {
                          if (element.MetadataCode !== "JPKCLM") {
                            const smCopy = {
                              ModuleName: element.MetadataName,
                              ModuleWebURL: `${sm.ModuleWebURL}/${element.MetadataCode}`
                            };
                            sm.SubModule.push(smCopy);
                          }
                        });
                      }
                    }
                    this.Menu.push(sm);
                  }
                }
              }
            }
          }
        }

        this.Menu = this.Menu.reverse();
        if (this.$route.path.startsWith("/learn/")) {
          this.$nextTick(() => {
            if (this.$refs.NavMenu) this.$refs.NavMenu.open("/learn/pec");
          });
        }
      }
    },
    MenuOpen(index, indexPath) {
      if (indexPath[0] === "/learn/pec") {
        if (this.$route.path !== indexPath[0]) this.$router.push(indexPath[0]);
      }
    },
    MenuClose(index, indexPath) {
      if (indexPath[0] === "/learn/pec") {
        if (this.$route.path !== indexPath[0]) this.$router.push(indexPath[0]);
        this.$refs.NavMenu.open("/learn/pec");
      }
    }
  }
};
</script>

<style lang="less">
.navbar {
  width: @NavBarWidth;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  background: #f5f5f5;

  .lump {
    @collapseWidth: 12px;
    @collapseHeight: 66px;
    position: absolute;
    left: @NavBarWidth;
    width: @collapseWidth;
    height: @collapseHeight;
    transition: all 0.12s ease;
    -webkit-transition: all 0.12s ease;
    top: 50%;
    z-index: 3;
    cursor: pointer;

    .inner {
      top: -50%;
      position: relative;
      transition: all 0.12s ease;
      -webkit-transition: all 0.12s ease;
      height: @collapseHeight;

      .bg {
        width: @collapseWidth;
        height: @collapseHeight;
        left: @NavBarWidth;
        right: auto;
        border-bottom: 8px solid transparent;
        border-right: none;
        border-left: @collapseWidth solid #ebebeb;
        border-top: 8px solid transparent;
        cursor: pointer;
      }

      i {
        cursor: pointer;
        position: absolute;
        top: 50%;
        left: -1px;
      }
    }
  }

  .lump:hover {
    transform: scale(1.05) translateY(2px);
    .inner {
      .bg {
        border-bottom: 7px solid transparent;
        border-right: none;
        border-left: 14px solid #dedede;
        border-top: 7px solid transparent;
      }
    }
  }

  .scene {
    .box-shadow(0, 0, 3px, rgba(0, 0, 0, 0.25));
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;

    @titleHeight: 48px;

    .title {
      background: #f5f5f5;
      height: @titleHeight;
      line-height: @titleHeight;
      font-size: 14px;
      text-indent: 16px;
      border-bottom: 1px solid #dedede;
      font-weight: 500;
    }

    .el-scrollbar {
      position: absolute;
      bottom: 0px;
      top: @titleHeight;
      left: 0px;
      right: 0px;

      .el-scrollbar__wrap {
        overflow-x: hidden;
      }
    }

    .el-menu {
      border-right: none;
      background-color: transparent;

      .is-active {
        color: #333;
        background-color: #dedede;

        > .el-submenu__title {
          font-weight: 700;
        }
      }

      .el-submenu.is-active {
        background-color: transparent;
      }

      .el-submenu,
      .el-menu-item {
        color: @FontColor;

        .is-active {
          font-weight: 700;
        }
      }

      // .el-submenu:after,
      // .el-submenu:not(:first-child)::before {
      //   content: "";
      //   width: 178px;
      //   height: 1px;
      //   background: #dedede;
      //   display: table;
      //   margin: 8px auto;
      // }

      .el-submenu__title:hover,
      .el-menu-item:focus,
      .el-menu-item:hover {
        color: #555;
        background-color: #ebebeb;
      }
    }
  }
}

.drag-pane {
  @NavBarWidth: 100%;
  .navbar {
    width: @NavBarWidth;
    .lump {
      left: @NavBarWidth;
    }
    .bg {
      left: @NavBarWidth;
    }
  }
}

.collapse,
.drag-pane .collapse {
  @navbarWidth: 0px;
  width: @navbarWidth;
  .lump {
    left: @navbarWidth;
    i {
      right: -1px;
      transform: rotate(180deg);
    }
  }
}
</style>
