<template>
  <div class="tbl" ref="div_Table" v-if="!IsDestroy">
    <div class="handle_bar" ref="HandleBar" v-if="ShowTopBar">
      <div class="search">
        <slot name="ExSearch" />
        <el-input
          v-intro="
            '模糊搜索：可以在搜索框中输入您想要搜索数据的关键字进行模糊搜索，也可以通过模糊指定内容进行搜索。'
          "
          ref="likeSearch"
          class="likeSearch"
          v-if="!isAdvSearch"
          :placeholder="
            SKey
              ? `请输入${SKeyName}模糊查询`
              : `选择${ModuleName}属性项搜索，或者输入关键字识别搜索`
          "
          v-model="SValue"
          maxlength="30"
          clearable
          show-word-limit
          @change="SearchValueChanged"
          @clear="Search"
          @keyup.enter.native="Search"
          size="mini"
        >
          <!-- @keydown.native="KeyHandle($event)" -->
          <el-select
            :disabled="isAdvSearch"
            slot="prepend"
            placeholder="请选择"
            v-model="SKey"
            @change="SearchKeyChanged"
            :popper-append-to-body="false"
            v-if="SearchKeyList && SearchKeyList.length > 0"
          >
            <el-option label="自动识别" value></el-option>
            <el-option
              v-for="(item, index) in SearchKeyList"
              :key="index"
              :label="item.Name"
              :value="item.Code"
            ></el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            :disabled="isAdvSearch"
            @click="Search"
          ></el-button>
        </el-input>
        <div
          class="advSearch"
          :class="{ inline: SearchForm.InLine }"
          ref="advSearch"
          v-else
        >
          <el-form
            :inline="SearchForm.InLine"
            size="mini"
            ref="SearchForm"
            :model="SearchForm"
            label-position="right"
            :label-width="SearchForm.LabelWidth"
          >
            <slot name="AdvSearchForm" />
            <el-form-item v-if="SearchForm.InLine">
              <el-button-group>
                <el-button type="primary" @click="Search" :loading="Loading"
                  >搜索</el-button
                >
                <el-button @click="Reset" :loading="Loading">清空</el-button>
              </el-button-group>
            </el-form-item>
          </el-form>
          <el-button-group v-if="!SearchForm.InLine">
            <el-button
              size="mini"
              :loading="Loading"
              type="primary"
              @click="Search"
              >搜索</el-button
            >
            <el-button size="mini" :loading="Loading" @click="Reset"
              >清空</el-button
            >
          </el-button-group>
        </div>
      </div>
      <div class="tools">
        <el-button
          v-intro="
            '高级搜索：点击这里可以切换至高级搜索模式，可以对数据进行精确检索。'
          "
          ref="btnChangeSearchModel"
          size="mini"
          type="text"
          v-if="SearchForm"
          @click="ChangeSearchModel"
          >{{ isAdvSearch ? "普通搜索" : "高级搜索" }}</el-button
        >
        <el-tooltip
          effect="light"
          class="refresh-btn"
          content="刷新"
          placement="top"
        >
          <el-button
            icon="el-icon-refresh"
            size="mini"
            type="info"
            plain
            v-if="ShowRefresh"
            @click="Search"
          ></el-button>
        </el-tooltip>
        <el-tooltip class="item" content="数据导入">
          <el-button
            icon="iconfont icon_import"
            size="mini"
            type="info"
            plain
            v-if="ShowImport"
            @click="dlgImportVisible = true"
          ></el-button>
        </el-tooltip>
        <el-tooltip class="item" placement="left" :disabled="HasCreatePower">
          <div slot="content">
            您未获得该模块的
            <b class="red">添加</b> 权限~~
          </div>
          <el-button
            ref="btnCreateObject"
            type="primary"
            icon="iconfont icon_plus"
            size="mini"
            v-if="ShowCreate"
            :disabled="!CreatePower || !HasCreatePower"
            @click="CreateHandle"
            >创建{{ ModuleName }}</el-button
          >
        </el-tooltip>
        <el-tooltip class="item" content="导出数据">
          <el-button
            :loading="SyncLoading"
            icon="iconfont icon_export"
            size="mini"
            type="info"
            plain
            v-if="ShowExport"
            @click="Export"
          ></el-button>
        </el-tooltip>
        <slot name="ExForm" />
      </div>
    </div>
    <div
      class="breadcrumb"
      ref="divBC"
      v-if="PathDatas && PathDatas.length > 0"
    >
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>
          <router-link :to="`${$route.meta.path}`">根节点</router-link>
        </el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item, index) in PathDatas" :key="index">
          <router-link :to="`${$route.meta.path}/${item.ID}`">
            {{ item.Name }}
          </router-link>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-table
      v-loading="Loading"
      element-loading-text="数据正在加载中"
      ref="Table"
      :data="Datas"
      :fit="true"
      class="tblData"
      style="width: 100%"
      :max-height="MaxTblHeight"
      @selection-change="SelectionChanged"
      @sort-change="SortChanged"
      @filter-change="FilterChanged"
      :row-class-name="Rname"
      :row-style="Rstyle"
      :row-key="KeyFileld"
    >
      <el-table-column
        class-name="sel_col"
        type="selection"
        width="35"
        v-if="ShowSelectColumn"
        fixed="left"
        :selectable="SelectableFun"
      ></el-table-column>
      <el-table-column
        :type="column.type"
        :class-name="column.Code"
        :header-align="column.Align"
        :align="column.Align"
        :prop="column.Code"
        :column-key="column.Code"
        :label="column.Header"
        :width="column.Width"
        :min-width="column.MinWidth"
        :fixed="column.Fixed"
        :sortable="column.Sort"
        :filter-multiple="column.FilterMultiple === true"
        :filters="column.Filters"
        :filtered-value="column.FilteredValue"
        v-for="(column, index) in Columns"
        :show-overflow-tooltip="column.Code !== 'action_column'"
        :key="index"
      >
        <template slot="header" v-if="column.HeaderSlot">
          <slot :name="`${column.Code}Header`" />
        </template>
        <template slot-scope="scope">
          <template v-if="column.type === 'index'">{{
            scope.$index + 1
          }}</template>
          <template
            v-if="!column.Slot && !column.IsEstate && column.type !== 'index'"
          >
            {{ scope.row[column.Code] }}
          </template>
          <i
            v-if="column.IsEstate"
            class="estate"
            :class="{
              'el-icon-circle-check': parseInt(scope.row[column.Code]) === 1,
              'el-icon-remove': parseInt(scope.row[column.Code]) === 0
            }"
            >{{ parseInt(scope.row[column.Code]) === 1 ? "正常" : "停用" }}</i
          >
          <slot
            v-if="column.Slot && !column.IsEstate"
            :name="column.Code"
            :row="scope.row"
            :index="index"
            :value="scope.row[column.Code]"
          />
          <el-button-group v-if="column.Slot && column.ShowBaseActionBtn">
            <el-button
              class="btnViewData"
              v-if="column.NeedViewBtn"
              onfocus="this.blur()"
              type="primary"
              icon="el-icon-view"
              size="mini"
              plain
              @click="ViewHandle(scope.row)"
              >查看</el-button
            >
            <el-tooltip class="item" placement="top" :disabled="HasEditPower">
              <div slot="content">
                您未获得该模块的
                <b class="red">修改</b> 权限~~
              </div>
              <el-button
                class="btnEditData"
                v-if="!column.UnNeedEditBtn"
                :disabled="scope.row['EditDisabled']"
                onfocus="this.blur()"
                type="primary"
                icon="el-icon-edit"
                size="mini"
                plain
                @click="EditHandle(scope.row, scope.$index)"
                >修改</el-button
              >
            </el-tooltip>
            <slot name="ExAction" :row="scope.row" />
            <el-tooltip class="item" placement="top" :disabled="HasEditPower">
              <div slot="content">
                您未获得该模块的
                <b class="red">修改</b> 权限~~
              </div>
              <el-button
                class="btnStopData"
                v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] == 1"
                :disabled="scope.row['EstateDisabled']"
                onfocus="this.blur()"
                plain
                size="mini"
                type="warning"
                icon="el-icon-remove-outline"
                @click="UpdateEstate(scope.row, 0)"
                >停用</el-button
              >
            </el-tooltip>
            <el-tooltip class="item" placement="top" :disabled="HasEditPower">
              <div slot="content">
                您未获得该模块的
                <b class="red">修改</b> 权限~~
              </div>
              <el-button
                class="btnRecoveryData"
                :disabled="scope.row['EstateDisabled']"
                onfocus="this.blur()"
                v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] != 1"
                plain
                type="success"
                icon="el-icon-circle-check"
                size="mini"
                @click="UpdateEstate(scope.row, 1)"
                >恢复</el-button
              >
            </el-tooltip>
            <el-tooltip class="item" placement="top" :disabled="HasDelPower">
              <div slot="content">
                您未获得该模块的
                <b class="red">删除</b> 权限~~
              </div>
              <el-button
                class="btnDelData"
                :disabled="scope.row['DelDisabled']"
                onfocus="this.blur()"
                size="mini"
                v-if="!column.UnNeedDelBtn"
                plain
                type="danger"
                icon="el-icon-close"
                @click="Delete(scope.row)"
                >删除</el-button
              >
            </el-tooltip>
          </el-button-group>
          <el-button-group
            v-if="column.Slot && column.ShowOrderBtn"
            class="order"
          >
            <el-tooltip placement="top" content="移至最顶部">
              <el-button
                onfocus="this.blur()"
                icon="iconfont icon_move_top"
                size="mini"
                plain
              ></el-button>
            </el-tooltip>
            <el-tooltip placement="top" content="上移">
              <el-button
                onfocus="this.blur()"
                icon="iconfont icon_move_up"
                size="mini"
                plain
              ></el-button>
            </el-tooltip>
            <el-tooltip placement="bottom" content="下移">
              <el-button
                onfocus="this.blur()"
                icon="iconfont icon_move_up"
                size="mini"
                plain
              ></el-button>
            </el-tooltip>
            <el-tooltip placement="bottom" content="移至最底部">
              <el-button
                onfocus="this.blur()"
                icon="iconfont icon_move_top"
                size="mini"
                plain
              ></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <div class="el-table el-table--fit" ref="TblFoot" v-if="ShowTblFoot">
      <div class="el-table__footer-wrapper">
        <table
          cellspacing="0"
          cellpadding="0"
          border="0"
          class="el-table__footer"
        >
          <colgroup>
            <col width="40" />
            <col width="100%" />
          </colgroup>
          <tfoot>
            <tr class>
              <td>
                <div class="cell" v-if="ShowSelectColumn">
                  <el-checkbox
                    v-model="CheckAll"
                    :indeterminate="isIndeterminate"
                    @change="CheckALLChanged"
                  ></el-checkbox>
                </div>
              </td>
              <td>
                <div class="cell">
                  <el-button-group v-if="ShowSelectColumn">
                    <el-tooltip
                      class="item"
                      placement="top"
                      :disabled="HasEditPower"
                    >
                      <div slot="content">
                        您未获得该模块的
                        <b class="red">修改</b> 权限~~
                      </div>
                      <el-button
                        v-if="!UnNeedEstateBtn"
                        icon="el-icon-remove-outline"
                        size="mini"
                        :disabled="Selections <= 0"
                        @click="UpdateEstate(null, 0)"
                        >停用</el-button
                      >
                    </el-tooltip>
                    <el-tooltip
                      class="item"
                      placement="top"
                      :disabled="HasEditPower"
                    >
                      <div slot="content">
                        您未获得该模块的
                        <b class="red">修改</b> 权限~~
                      </div>
                      <el-button
                        v-if="!UnNeedEstateBtn"
                        icon="el-icon-circle-check"
                        size="mini"
                        :disabled="Selections <= 0"
                        @click="UpdateEstate(null, 1)"
                        >恢复</el-button
                      >
                    </el-tooltip>
                    <el-tooltip
                      class="item"
                      placement="top"
                      :disabled="HasDelPower"
                    >
                      <div slot="content">
                        您未获得该模块的
                        <b class="red">删除</b> 权限~~
                      </div>
                      <el-button
                        v-if="!UnNeedDelBtn"
                        icon="el-icon-close"
                        size="mini"
                        :disabled="Selections <= 0"
                        @click="Delete()"
                        >删除</el-button
                      >
                    </el-tooltip>
                    <slot name="BatchAction" />
                    <slot name="BatchWhite" />
                  </el-button-group>
                  <el-pagination
                    v-loading="Loading"
                    :current-page.sync="PIndex"
                    :page-size.sync="PSize"
                    :total="TotalCounts"
                    @size-change="PageSizeChanged"
                    @current-change="PageChanged"
                    layout="total, sizes, prev, pager, next, jumper"
                  ></el-pagination>
                </div>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <!-- 基本对话框 -->
    <el-dialog
      center
      ref="DlgBaseForm"
      custom-class="dlgBaseForm"
      :title="`${IsEdit ? '修改' : '创建'}${ModuleName}`"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal-append-to-body="false"
      :close-on-press-escape="!SyncLoading"
      @close="CloseBase"
      :destroy-on-close="destroyOnClose"
    >
      <el-form
        ref="BaseForm"
        v-loading="FormLoading"
        :disabled="SyncLoading"
        label-position="right"
        :model="BaseForm"
        :rules="BaseFormRule"
        :label-width="
          BaseForm && BaseForm.LabelWidth ? BaseForm.LabelWidth : '120px'
        "
        size="mini"
        status-icon
      >
        <slot name="BaseForm" />
      </el-form>
      <div slot="footer">
        <slot name="CustomeActionBtn" />
        <el-button-group v-if="!CustomeActionBtn">
          <slot name="ExDlgAction" />
          <el-button
            :loading="SyncLoading"
            type="primary"
            icon="el-icon-check"
            size="mini"
            @click="ConfirmBaseForm"
            v-if="ConfirmBtn"
            autofocus
            native-type="submit"
            >确 定</el-button
          >
          <el-button
            icon="el-icon-close"
            size="mini"
            @click="dialogVisible = false"
            >取 消</el-button
          >
        </el-button-group>
      </div>
    </el-dialog>
    <!-- 导入对话框 -->
    <el-dialog
      center
      custom-class="dlgImport"
      :close-on-click-modal="false"
      :visible.sync="dlgImportVisible"
      :modal-append-to-body="false"
      :close-on-press-escape="!UploadLoding"
      @closed="DlgImportClosed"
      :show-close="false"
    >
      <template slot="title">
        <label class="el-dialog__title">数据导入</label>
        <span>
          导入过程中请不要
          <strong>关闭浏览器</strong> 或
          <strong>刷新页面</strong>
        </span>
      </template>
      <el-steps :active="UploadStep" finish-status="success" align-center>
        <el-step title="上传需要导入的数据文件"></el-step>
        <el-step title="数据导入"></el-step>
      </el-steps>
      <el-upload
        class="upload"
        v-if="UploadStep === 0"
        ref="upload"
        action
        :class="{ limit: fileList.length >= FileLimit }"
        :limit="FileLimit"
        :file-list="fileList"
        :auto-upload="false"
        :on-change="UploadChange"
        :on-remove="RemoveHandle"
        :accept="UploadExcelFileType"
        v-loading="UploadLoding"
        element-loading-text="文件上传中"
      >
        <el-button
          type="primary"
          icon="el-icon-plus"
          slot="trigger"
          size="small"
          >选取文件</el-button
        >
        <div slot="tip" class="el-upload__tip">
          只能上传Excel(xlsx/xls)文件，文件最大2M
        </div>
      </el-upload>
      <el-table
        :data="UploadDatas"
        height="420"
        :fit="true"
        style="width: 100%"
        v-if="UploadStep === 1"
      >
        <el-table-column
          :class-name="column.Code"
          :align="column.Align"
          :prop="column.Code"
          :column-key="column.Code"
          :label="column.Header"
          :width="column.Width"
          v-for="(column, index) in UploadColumns"
          :key="index"
        ></el-table-column>
        <el-table-column class-name="estate" width="140" label="状态">
          <template slot-scope="scope">
            <i class="el-icon-circle-check" v-if="scope.row.Uploadstate === 1"
              >成功</i
            >
            <i
              class="el-icon-circle-close"
              v-if="
                scope.row.Uploadstate &&
                  scope.row.Uploadstate != 0 &&
                  scope.row.Uploadstate != 1
              "
              >{{ scope.row.Uploadstate }}</i
            >
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer">
        <el-button
          v-if="UploadStep === 0"
          type="primary"
          icon="el-icon-download"
          size="mini"
          @click="DownloadTmpFile"
          >下载模板</el-button
        >
        <el-progress
          :text-inside="true"
          :stroke-width="18"
          :percentage="UploadProgress"
          v-if="UploadStep === 1"
        ></el-progress>
        <el-button-group>
          <el-button
            size="mini"
            icon="el-icon-back"
            @click="PrevUpload"
            :disabled="UploadStep === 0 || UploadLoding"
            >上一步</el-button
          >
          <el-button
            v-if="UploadStep === 0"
            :disabled="!fileList || fileList.length < FileLimit"
            type="primary"
            icon="el-icon-upload"
            size="mini"
            @click="Upload"
            :loading="UploadLoding"
            >上传</el-button
          >
          <el-button
            id="btnUpload"
            v-if="UploadStep === 1"
            type="primary"
            icon="el-icon-upload2"
            size="mini"
            @click="ImportData"
            :loading="UploadLoding"
            >导入</el-button
          >
          <el-button
            icon="el-icon-close"
            size="mini"
            @click="dlgImportVisible = false"
            :disabled="UploadLoding"
            >关 闭</el-button
          >
        </el-button-group>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Sortable from "sortablejs";
import api from "@/scripts/api";
import store from "@/scripts/store";
import $ from "jquery";
export default {
  props: {
    // 可拖拽行
    RowDraggable: {
      type: Boolean,
      default: false
    },
    // 主键字段
    KeyFileld: {
      type: String,
      default: ""
    },
    // 状态字段
    EstateFiled: {
      type: String,
      default: ""
    },
    // 加载中
    Loading: {
      type: Boolean,
      default: false
    },
    // 窗体加载中
    FormLoading: {
      type: Boolean,
      default: false
    },
    // 显示创建按钮
    ShowCreate: {
      type: Boolean,
      default: true
    },
    // 自定义创建
    CustomCreate: {
      type: Boolean,
      default: false
    },
    // 显示顶部操作条
    ShowTopBar: {
      type: Boolean,
      default: true
    },
    // 显示导出按钮
    ShowExport: {
      type: Boolean,
      default: true
    },
    // 显示导入按钮
    ShowImport: {
      type: Boolean,
      default: false
    },
    // 显示刷新按钮
    ShowRefresh: {
      type: Boolean,
      default: false
    },
    /**
     * 页脚不需要状态操作按钮
     */
    UnNeedEstateBtn: {
      type: Boolean,
      default: false
    },
    /**
     * 页脚不需要删除操作按钮
     */
    UnNeedDelBtn: {
      type: Boolean,
      default: false
    },
    // 搜索键
    SearchKey: {
      type: String,
      default: ""
    },
    // 搜索值
    SearchValue: {
      type: String,
      default: ""
    },
    // 模块名称
    ModuleName: {
      type: String,
      default: ""
    },
    // 模块代码
    ModuleCode: {
      type: String,
      default: ""
    },
    SearchKeyList: {
      type: Array,
      default: () => []
    },
    Datas: {
      type: Array,
      default: () => []
    },
    // 显示选择列
    ShowSelectColumn: {
      type: Boolean,
      default: true
    },
    // 列
    Columns: {
      type: Array,
      default: () => []
    },
    // 面包屑 路径数据
    PathDatas: {
      type: Array,
      default: () => []
    },
    // 当前第几页
    PageIndex: {
      type: Number,
      default: 1
    },
    // 分页大小
    PageSize: {
      type: Number,
      default: 20
    },
    // 总行数
    TotalCounts: {
      type: Number,
      default: 0
    },
    // 排序字段
    SortField: {
      type: String,
      default: ""
    },
    // 排序方向
    SortDir: {
      type: Number,
      default: 0
    },
    // 高级搜索
    AdvSearch: {
      type: Boolean,
      default: false
    },
    // 高级搜索表单
    SearchForm: {
      type: Object,
      default: null
    },
    // 基础表单
    BaseForm: {
      type: Object,
      default: null
    },
    // 基础表单规则
    BaseFormRule: {
      type: Object,
      default: null
    },
    // 上传列
    UploadColumns: {
      type: Array,
      default: () => []
    },
    // 上传的模板名称
    UploadTemplateName: {
      type: String,
      default: ""
    },
    // 复选框是否可以选中
    SelectableFun: {
      type: Function,
      default: row => true
    },
    // 行class
    Rname: {
      type: Function,
      default: row => "none"
    },
    // 行style
    Rstyle: {
      type: Function,
      default: row => "none"
    },
    CreatePower: {
      type: Boolean,
      default: true
    },
    // 基础表单自定义按钮
    CustomeActionBtn: {
      type: Boolean,
      default: false
    },
    // 最大高度是否为空
    showMaxeight: {
      type: Boolean,
      default: true
    },
    // 确定按钮是否显示
    ConfirmBtn: {
      type: Boolean,
      default: true
    },
    // 是否显示表格底部
    ShowTblFoot: {
      type: Boolean,
      default: true
    },
    // 关闭时销毁 Dialog 中的元素
    destroyOnClose: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    $route: function(newRouter, oldRouter) {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    PageIndex(newVal, oldVal) {
      this.PIndex = newVal;
    },
    SearchValue(newVal, oldVal) {
      this.SValue = newVal;
    },
    SearchKey(newVal, oldVal) {
      this.SKey = newVal;
    },
    AdvSearch(newVal, oldVal) {
      this.isAdvSearch = newVal;
    },
    PathDatas(newVal, oldVal) {
      if (newVal && newVal.length > 0) {
        this.$nextTick(() => {
          this.AdjustHeight();
        });
      }
    },
    ModuleCode(newVal, oldVal) {
      this.ModuleCode = newVal;
      if (
        this.$route.path.startsWith("/p/") ||
        this.$route.path.startsWith("/ps/")
      ) {
        this.HasCreatePower = true;
        this.HasEditPower = true;
        this.HasDelPower = true;
      } else {
        this.HasCreatePower = store.VerifyPower(
          this.ModuleCode,
          store.ActionKey.Add
        );
        this.HasEditPower = store.VerifyPower(
          this.ModuleCode,
          store.ActionKey.Edit
        );
        this.HasDelPower = store.VerifyPower(
          this.ModuleCode,
          store.ActionKey.Del
        );
      }
    }
  },
  data() {
    return {
      IsDestroy: false,
      msgg: "",
      dialogVisible: false,
      dlgImportVisible: false,
      SKey: this.SearchKey,
      SKeyName: "",
      SValue: this.SearchValue,
      CheckAll: false,
      isIndeterminate: false,
      MaxTblHeight: null,
      isAdvSearch: this.AdvSearch,
      PIndex: this.PageIndex,
      PSize: this.PageSize,
      Selections: [],
      IsEdit: false,
      SyncLoading: false,
      FileLimit: 1,
      fileList: [],
      UploadStep: 0,
      UploadLoding: false,
      UploadDatas: [],
      UploadProgress: 0,
      HasCreatePower: true,
      HasEditPower: true,
      HasDelPower: true,
      sortable: null
    };
  },
  created() {
    this.$nextTick(() => {
      this.AdjustHeight();
    });
  },
  mounted() {
    if (this.RowDraggable) this.RowDrop();
    this.$nextTick(() => {
      this.AdjustHeight();
    });

    window.onresize = () => {
      this.$nextTick(() => {
        this.AdjustHeight();
      });
    };
  },
  beforeDestroy() {
    window.onresize = null;
    this.IsDestroy = true;
    if (this.sortable) {
      this.sortable.destroy();
    }
    console.log("Table 释放");
  },
  updated() {
    this.$nextTick(() => {
      this.AdjustHeight();
    });
  },
  methods: {
    RowDrop() {
      this.$nextTick(() => {
        const tbody = this.$refs.Table.$el.querySelector(
          ".el-table__body tbody"
        );
        this.sortable = Sortable.create(tbody, {
          handle: ".cell", // Drag handle selector within list items
          // 拖拽结束
          onEnd: ({ item, newIndex, oldIndex }) => {
            const currRow = this.Datas.splice(oldIndex, 1)[0];
            this.Datas.splice(newIndex, 0, currRow);
            this.$emit("RowDropEnd");
          }
        });
      });
    },
    // KeyHandle(e) {
    //   if (e.keyCode === 13) {
    //     this.Search();
    //   }
    // },
    CloseBase() {
      this.$emit("CloseBase");
    },
    /**
     * 切换搜索模式
     */
    ChangeSearchModel() {
      this.isAdvSearch = !this.isAdvSearch;
      this.$emit("update:AdvSearch", this.isAdvSearch);
      this.$nextTick(() => {
        this.AdjustHeight();
        this.$refs.Table.doLayout();
      });
    },
    SearchKeyChanged() {
      const find = this.SearchKeyList.find(item => item.Code === this.SKey);
      if (find) this.SKeyName = find.Name;
      this.$emit("update:SearchKey", this.SKey);
    },
    SearchValueChanged() {
      this.$emit("update:SearchValue", this.SValue);
    },
    /**
     * 高度调整
     */
    AdjustHeight() {
      this.$nextTick(() => {
        const tblHeight = window.getComputedStyle(this.$refs.div_Table).height;

        const hbHeight = this.$refs.HandleBar
          ? window.getComputedStyle(this.$refs.HandleBar).height
          : 0;
        const tfootHeight = this.$refs.TblFoot
          ? window.getComputedStyle(this.$refs.TblFoot).height
          : 0;
        const bcHeight =
          this.PathDatas && this.PathDatas.length > 0
            ? window.getComputedStyle(this.$refs.divBC).height
            : 0;
        if (this.showMaxeight) {
          this.MaxTblHeight =
            parseInt(tblHeight) -
            parseInt(hbHeight) -
            3 -
            parseInt(tfootHeight) -
            parseInt(bcHeight);
        }
      });
    },
    /**
     * 当选择项发生变化时会触发该事件
     */
    SelectionChanged(val) {
      if (val && val.length > 0) {
        if (val.length === this.Datas.length) {
          this.CheckAll = true;
          this.isIndeterminate = false;
        } else {
          this.CheckAll = false;
          this.isIndeterminate = true;
        }
      } else {
        this.CheckAll = false;
        this.isIndeterminate = false;
      }

      this.$emit("update:selections", val);
      this.Selections = val;
    },
    /**
     * 底部的复选框切换
     */
    CheckALLChanged() {
      this.$refs.Table.toggleAllSelection();
    },
    /**
     * 当表格的排序条件发生变化的时候会触发该事件
     */
    SortChanged(orderInfo) {
      if (orderInfo) {
        const column = this.Columns.find(c => c.Code === orderInfo.prop);
        this.$emit(
          "update:SortField",
          column.SortField ? column.SortField : orderInfo.prop
        );
        this.$emit("update:SortDir", orderInfo.order === "descending" ? 1 : 0);
        this.$emit("update:PageIndex", 1);
        this.$emit("LoadData");
      }
    },
    /**
     * 当表格的筛选条件发生变化的时候会触发该事件，参数的值是一个对象，对象的 key 是 column 的 columnKey，对应的 value 为用户选择的筛选条件的数组。
     */
    async FilterChanged(filters) {
      try {
        const key = Object.keys(filters)[0];
        // 动态同步父组件的筛选值。
        const column = this.Columns.find(c => c.Code === key);

        let value = filters[key][0];

        if (column && column.FilterMultiple) {
          value = "";
          await filters[key].forEach((f, i) => {
            value += f;
            if (i < filters[key].length - 1) {
              value += ",";
            }
          });
        }

        this.$emit("update:PageIndex", 1);
        this.$emit(`update:${key}`, value);
        this.$emit("LoadData");
      } catch {}
    },
    /**
     * 搜索
     */
    Search() {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    /**
     * 刷新直接重新加载数据
     */
    Refresh() {
      this.$emit("LoadData");
    },
    /**
     * 分页大小改变时会触发
     */
    PageSizeChanged(val) {
      this.$emit("update:PageIndex", 1);
      this.$emit("update:PageSize", val);
      this.$emit("LoadData");
    },
    /**
     * 切换分页时触发
     * @param {number} val 新的页码
     */
    PageChanged(val) {
      console.log(val);
      this.$emit("update:PageIndex", val);
      this.$emit("update:PageSize", this.PageSize);
      this.$emit("LoadData");
    },
    /**
     * 更新状态
     */
    async UpdateEstate(row, estate) {
      if (this.HasEditPower) {
        if (row) {
          this.$emit("UpdateEstate", row[this.KeyFileld], estate);
        } else {
          if (this.Selections.length > 0) {
            let ids = "";
            await this.Selections.forEach((row, index) => {
              ids += row[this.KeyFileld];
              if (index < this.Selections.length - 1) ids += ",";
            });

            this.$emit("UpdateEstate", ids, estate);
          }
        }
      }
    },
    /**
     * 删除
     */
    Delete(row) {
      if (this.HasDelPower) {
        this.$confirm("此操作将永久删除这些数据, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(async () => {
            if (row) {
              this.$emit("Delete", row[this.KeyFileld]);
            } else {
              if (this.Selections.length > 0) {
                let ids = "";
                await this.Selections.forEach((row, index) => {
                  ids += row[this.KeyFileld];
                  if (index < this.Selections.length - 1) ids += ",";
                });

                this.$emit("Delete", ids);
              }
            }
          })
          .catch(() => {});
      }
    },
    /**
     * 重置高级搜索
     */
    Reset() {
      this.$refs.SearchForm.resetFields();
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    /**
     * 导入数据
     */
    async ImportData() {
      if (this.UploadDatas && this.UploadDatas.length > 0) {
        this.UploadLoding = true;

        this.UploadDatas.forEach((element, index) => {
          $("#btnUpload").queue("ImportData", async () => {
            await this.$emit(
              "ImportData",
              element,
              res => {
                this.UploadProgress = parseFloat(
                  (((index + 1) / this.UploadDatas.length) * 100).toFixed(2)
                );

                if (res.Result) {
                  this.$set(element, "Uploadstate", 1);
                } else {
                  this.$set(element, "Uploadstate", res.Info);
                }

                if (index + 1 === this.UploadDatas.length) {
                  this.$emit("LoadData");
                  this.UploadLoding = false;
                }
                $("#btnUpload").dequeue("ImportData");
              }, // 回调
              () => {
                this.UploadLoding = false;
              } // 失败后的回调
            );
          });
        });

        $("#btnUpload").dequeue("ImportData");
      }
    },
    /**
     * 上一步
     */
    PrevUpload() {
      this.UploadStep--;
    },
    /**
     * 取消上传
     */
    DlgImportClosed() {
      this.dlgImportVisible = false;
      this.UploadStep = 0;
      this.UploadProgress = 0;
      this.fileList = [];
      this.UploadDatas = [];
    },
    /**
     * 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
     */
    UploadChange(file, fileList) {
      this.fileList = fileList;
    },
    /**
     * 文件列表移除文件时的钩子
     */
    RemoveHandle(file, fileList) {
      this.fileList = fileList;
    },
    /**
     * 下载模板
     */
    async DownloadTmpFile() {
      window.location.href =
        this.$root.DownloadURL +
        "?fileName=" +
        this.UploadTemplateName +
        "&fileType=3";
    },
    /**
     * 上传
     */
    async Upload() {
      if (this.fileList && this.fileList.length > 0) {
        this.UploadLoding = true;
        const fd = new FormData();
        fd.append("stream", this.fileList[0].raw);
        fd.append("fileType", 3);
        const res = await api.Common.UploadFile(fd);
        if (res) {
          await this.$emit(
            "LoadUploadData",
            res.Data,
            datas => {
              console.log(datas);
              this.UploadDatas = datas;
              this.UploadStep++;
              this.UploadLoding = false;
            }, // 成功后的回调
            () => {
              this.$message({
                showClose: true,
                message: "数据有误",
                type: "error",
                duration: 1000
              });
              this.UploadLoding = false;
            } // 失败后的回调
          );
        }
      }
    },
    /**
     * 导出按钮
     */
    async Export() {
      this.SyncLoading = true;
      await this.$emit("ExportHandle", () => {
        this.SyncLoading = false;
      });
    },
    /**
     * 创建按钮点击
     */
    CreateHandle() {
      if (this.HasCreatePower) {
        this.IsEdit = false;
        this.$emit("BeforeCreateHandle");
        if (this.CustomCreate) {
          this.$emit("CreateHandle");
        } else {
          this.dialogVisible = true;
          this.$nextTick(() => {
            if (this.ModuleCode !== "RBPlan") {
              this.$refs["BaseForm"].resetFields();
            }
            this.$emit("CreateHandle");
          });
        }
      }
    },
    /**
     * 编辑行按钮点击
     */
    EditHandle(row, index) {
      if (this.HasEditPower) {
        this.IsEdit = true;
        this.$emit("BeforeEditHandle", row);
        this.dialogVisible = true;

        this.$nextTick(() => {
          try {
            this.$refs["BaseForm"].resetFields();
          } catch (e) {}

          this.$emit("EditRowHandle", row, index);
        });
      }
    },
    /**
     * 查看行按钮点击
     */
    ViewHandle(row) {
      this.$emit("ViewRowHandle", row);
    },
    /**
     * 基础表单提交
     */
    ConfirmBaseForm() {
      this.SyncLoading = true;
      this.$refs.BaseForm.validate(async valid => {
        if (valid) {
          await this.$emit(
            "ConfirmBaseForm",
            () => {
              this.dialogVisible = false;
              this.$emit("update:PageIndex", 1);
              this.$emit("LoadData");
              this.SyncLoading = false;
            },
            () => {
              this.SyncLoading = false;
            }
          );
        } else {
          this.SyncLoading = false;
        }
      });
    }
  }
};
</script>

<style lang="less">
.tbl {
  margin-top: @Margin;
  height: calc(100% - @Margin * 2) !important;

  .handle_bar {
    display: grid;
    grid-template-columns: 1fr auto;

    .search {
      display: inline-block;

      .el-range-editor {
        margin-right: 5px;
      }

      .likeSearch {
        width: 515px;
      }
      .likeSearch.el-input-group--prepend {
        width: 616px;
      }

      .el-select {
        .el-input__inner {
          width: 100px;
        }

        .el-select-dropdown__item.selected {
          font-weight: 500;
        }
      }

      // .el-input-group {
      //   > .el-input__inner {
      //     // width: 460px;
      //   }
      // }

      .el-button.is-disabled,
      .el-button.is-disabled:focus,
      .el-button.is-disabled:hover {
        border-color: transparent;
        background-color: transparent;
      }

      .advSearch {
        position: relative;
        width: 99%;

        .el-form {
          width: calc(100% - 111px);
          .el-form-item__label {
            font-size: 12px;
          }
          .el-form-item {
            margin-bottom: 5px;
          }
        }
        .el-form--inline {
          .el-form-item {
            margin-bottom: 5px;
          }
        }

        .el-button-group {
          position: absolute;
          bottom: 5px;
          right: -5px;
        }
      }
      .advSearch.inline {
        .el-form {
          width: 100%;
        }
        .el-button-group {
          position: relative;
          bottom: 0px;
        }
      }
    }

    .tools {
      .el-button {
        i {
          font-size: 13px;
        }
      }

      .el-button.item {
        padding: 5px 10px;
        .iconfont {
          font-size: 15px;
        }
      }

      .el-button.refresh-btn {
        padding: 5px 10px;
        i {
          font-size: 15px;
        }
      }

      .el-button {
        margin-left: 3px;
      }

      .el-button--text + .el-button {
        margin-left: 10px;
      }

      .el-button--info.is-plain {
        background-color: #f7f7f7;
      }

      .el-button--info.is-plain:focus,
      .el-button--info.is-plain:hover {
        background: #fff;
        border-color: #ddd;
        color: #333;
      }
    }
  }

  .breadcrumb {
    display: inline-block;

    .el-breadcrumb {
      border-left: 2px solid #88b7e0;
      color: #999;
      margin: 5px 10px 0px 0px;
      font-size: 12px;
      padding-left: 8px;

      .el-breadcrumb__inner a,
      .el-breadcrumb__inner.is-link {
        font-weight: 500;
      }
    }
  }

  .dlgBaseForm {
    min-width: 800px;
  }

  .dlgImport {
    width: 1200px;

    .el-dialog__header {
      position: relative;

      span {
        position: absolute;
        left: 10px;

        strong {
          color: red;
        }
      }
    }

    .el-dialog__body {
      position: relative;
      text-align: center;
      min-height: 470px;

      .el-steps {
        text-align: left;
      }

      > .el-button {
        margin-top: 10% !important;
      }

      .upload {
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        padding-top: 60px + 80px;
        text-align: center;

        > *:not(.el-loading-mask) {
          width: 500px;
          margin: auto;
        }

        .el-upload__tip {
          text-align: center;
          margin-top: 10px;
        }

        .el-upload {
          display: initial;
        }
      }

      .el-upload-list__item-name {
        margin-right: 0px;
      }

      .upload.limit {
        .el-button:first-child {
          color: #8cc5ff;
          background-color: #ecf5ff;
          border-color: #d9ecff;
          cursor: not-allowed;

          * {
            cursor: not-allowed;
          }
        }
      }

      .el-table {
        margin-top: 10px;

        td.estate {
          i::before {
            margin-right: 5px;
          }
          .el-icon-circle-check {
            .green();
          }
          .el-icon-circle-close {
            .red();
          }
        }
      }
    }

    .el-dialog__footer {
      > div {
        position: relative;

        > button,
        .el-progress {
          position: absolute;
          left: 0px;
        }

        .el-progress {
          margin-top: 7px;
          width: 800px;
        }
      }
    }
  }
}

.weekCal + .tbl {
  height: calc(100% - @Margin * 3 - 105px) !important;
}

.el-table {
  font-size: 12px;
  margin-top: 3px;
  border: 1px solid #e1e6eb;

  .el-table__header {
    thead {
      tr {
        th {
          padding: 2px 5px;
          font-weight: normal;
          color: #999;
          border-bottom: 1px solid #e1e6eb;
          background-color: #f5f6fa;

          .cell {
            padding-left: 5px;
            padding-right: 5px;

            .el-select {
              width: 95%;
            }
          }
        }

        .is-sortable {
          .cell {
            display: flex;
            align-items: center;
          }
        }
      }
    }
  }

  .el-table__footer {
    tfoot {
      tr {
        td {
          padding: 5px;
          border-bottom: none;
        }
      }
    }
  }

  .el-table__body {
    tbody {
      tr {
        td {
          padding: 5px;
          border-top: 0px;
          color: @FontColor;
          border-bottom: 1px solid #e1e6eb;
          vertical-align: middle;
          .estate {
            font-size: 12px;
          }

          .estate:before {
            margin-right: 3px;
          }

          .estate.el-icon-circle-check {
            color: #67c23a;
          }

          .estate.el-icon-remove {
            color: #f56c6c;
          }
        }

        // 操作列公共处理
        .action_column {
          .cell {
            text-overflow: clip;
          }

          .el-button-group + .el-button-group {
            margin-left: 5px;
          }

          .el-button-group.order {
            .el-button:nth-of-type(n + 3) {
              .transform(180deg);
            }
          }
        }
      }

      tr:last-child {
        td {
          border-bottom: 0px;
        }
      }
    }
  }

  .cell {
    padding-left: 5px;
    padding-right: 5px;

    .iconfont {
      font-size: 12px;
    }
  }
}

.el-table + .el-table {
  margin-top: 0px;
  border-top: none;
}

.el-table__fixed-right::before,
.el-table__fixed::before {
  content: none;
}

.el-table__fixed-right-patch {
  background-color: #f5f6fa;
}

.el-table--border::after,
.el-table--group::after,
.el-table::before {
  height: 0px;
  background-color: transparent;
}

.el-pagination {
  @FontColor: #888;

  float: right;

  button,
  .el-pager li {
    font-size: 12px;
    color: #999999;
    background-color: #ffffff;
    border: 1px solid #dddddd;
  }

  button:not(:disabled):hover,
  .el-pager li:hover {
    color: #fff;
    background-color: fade(@ThemeColor, 60%);
    border-color: fade(@ThemeColor, 60%);
  }

  button:disabled {
    color: #bbb;
    border: 1px solid #ddd;
    background-color: #f7f7f7;
    i {
      cursor: not-allowed;
    }
  }

  .el-pager {
    li:first-child {
      border-left: none;
    }

    li + li {
      border-right: none;
    }

    li.active {
      color: #ffffff;
      cursor: default;
      background-color: @ThemeColor;
      border: 1px solid @ThemeColor;
    }
  }

  .btn-prev,
  .btn-next {
    i {
      display: block;
    }
  }

  .btn-prev {
    padding-right: 6px;
  }

  .btn-next {
    padding-left: 6px;
  }

  button,
  span:not([class*="suffix"]) {
    font-size: 12px;
  }

  .el-pagination__jump {
    .el-input {
      margin-left: 5px;
      margin-right: 5px;
      font-size: 12px;
    }
  }

  .el-pagination__total {
    color: @FontColor;
  }

  .el-pagination__sizes {
    .el-input {
      .el-input__inner {
        font-size: 12px;
        padding-right: 22px;
        color: @FontColor;
      }
    }
  }
}
</style>
